  /* text-align: center; */
  color: #fff;
  border-radius: 25px;
  cursor: pointer;
  font-size: 20px;
  margin-left: -34px;
}
.outer_loader {
  position: fixed;
  top: 0;
  background: #080000db;
  left: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  z-index: 9999;
}
.loader {
  border: 5px solid #f3f3f3;
  border-radius: 50%;
  border-top: 5px solid #3498db;
  width: 80px;
  height: 80px;
  animation: spin 2s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;

}
.insiderLoader {
  display: none;
}
/* Safari */
@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
